@import "/node_modules/ol/ol.css";

html,
body {
    font-family: Aspekta, sans-serif;
    margin: 0;
    height: 100%;
}

.container {
    display: flex;
    flex-direction: row;
    padding: 24px 16px 24px 16px;
    justify-content: space-around;

}

.ol-control button {
    background-color: rgba(121, 85, 72, 1) !important;
    color: white !important;
    border-color: white !important;
}

.statistics-pane {
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    padding: 0 8px;
    width: 40vw;
    align-items: center;

    border: 1px solid rgba(121, 85, 72, 0.2);
    -webkit-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
    -moz-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
    box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
}

#map {
    position: relative;
    height: 724px;
    width: 100%;
}

.map-container {
    width: 40vw;
}

@media only screen and (max-width: 1500px) {
    .statistics-pane, .map-container {
        width: 600px;
    }
}

@media only screen and (max-width: 1300px) {
    .statistics-pane, .map-container {
        width: 500px;
        border: none;
        box-shadow: none;
    }

    .container {
        padding: 24px 8px;
    }
}

@media only screen and (max-width: 1200px) {
    .statistics-pane, .map-container {
        width: 400px;
        border: none;
        box-shadow: none;
    }

    .container{
        padding: 24px 0;
    }
}

@media only screen and (max-width: 1050px) {
    .statistics-pane, .map-container {
        width: 90vw;
    }

    .statistics-pane {
        margin-top: 96px;
        border: 1px solid rgba(121, 85, 72, 0.2);
        -webkit-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
        -moz-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
        box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.1);
    }

    .container{
        padding: 24px 16px;
        flex-direction: column;
        align-items: center;
    }
}

#info {
    position: absolute;
    display: inline-block;
    height: auto;
    width: auto;
    z-index: 100;
    background-color: rgba(255, 255, 255, 0.9);
    border: 1px solid rgba(0, 0, 0, 0.05);;
    color: #000;
    border-radius: 4px;
    padding: 8px;
    left: 50%;
    transform: translateX(3%);
    visibility: hidden;
    pointer-events: none;
    font-size: 12px;
    font-weight: 500;
    -webkit-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.22);
    -moz-box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.22);
    box-shadow: 1px 4px 20px 0 rgba(0,0,0,0.22);
}


#info p {
    margin: 0;
    padding: 0 0 4px;
}

span {
    color: #7e7e7e ! important;
    font-size: 11px ! important ;
    font-weight: 600 ! important ;
    text-align: right ! important ;
}

.info-type {
    display: inline;
    font-size: 11px;
    color: #4f4f4f;
    font-weight: 600;
    text-align: left;
}

.country-name {
    color: #383838 !important;
    font-size: 13px !important ;
    text-align: center !important;
    font-weight: 600 !important;
    margin-bottom: 12px !important;
    width:100%;
    border-bottom: 1px solid #e3e3e3;
}

.masked-image-0 {}
.masked-image--20deg { filter: hue-rotate(-20deg); }
.masked-image-70deg { filter: hue-rotate(70deg); }
.masked-image-30deg { filter: hue-rotate(30deg); }
.masked-image-20deg { filter: hue-rotate(20deg); }
.masked-image-10deg { filter: hue-rotate(10deg); }
.masked-image-90deg { filter: hue-rotate(90deg); }

.doughnut {
    margin:24px 0;
    width: 500px;
    height: 400px;
}

.line-graph {
    margin:0 0 24px 0;
    width: 600px;
}

.chart-year-range-slider, .map-year-slider {
    width: 100%;
    position: relative;
    padding: 24px 0 12px 0;
}

.chart-year-range-thumb, .map-year-slider-thumb {
    position: absolute;
    top: 30%;
    width: 40px;
    height: 40px;
    color: #fff;
    font-size: 13px;
    font-weight: 600;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    background-color: rgba(121, 85, 72, 1);
    cursor: pointer;
    display: flex;
    border: 1px solid #cecece;
}

.chart-year-range-track, .map-year-slider-track  {
    height: 10px;
    background-color: rgba(121, 85, 72, 0.3);
    border: 1px solid #cecece;
    border-radius: 4px;
}

.slider-subtext{
    font-size: 13px;
    font-style: italic;
    text-align: center;
}

.map-title {
    font-size: 13px;
    font-weight: 600;
}

.loading-animation {
    position: absolute;
    z-index: 999;
    bottom: 0;
    right: 0;
    width:40px;
    height:30px;
}
